import * as React from "react";
import { Container, Navigation, Footer } from "hds-react";
import { navigate } from "gatsby";
import "./layout.scss";

const Layout = ({ title, path, children }) => {
  return (
    <main className="main">
      <title>{title}</title>
      <Navigation
        id="page-content"
        title="Helsinki Design System"
        menuToggleAriaLabel="menu"
        skipTo="#page-content"
        skipToContentLabel="Skip to content"
      >
        <Navigation.Row variant="inline">
          <Navigation.Item
            label="Getting started"
            active={path.startsWith("/get_started/")}
            onClick={() => navigate("/get_started/")}
          />
          <Navigation.Item label="Tutorials" />
          <Navigation.Item label="Guidelines" />
          <Navigation.Item label="Visual assets" />
          <Navigation.Item label="Design tokens" />
          <Navigation.Dropdown
            label="Components"
            active={path.startsWith("/components/")}
            onClick={() => navigate("/components/")}
          >
            <Navigation.Item href="#" label="Overview" onClick={(e) => navigate("/components/")} />
            <Navigation.Item href="#" label="Buttons" onClick={(e) => navigate("/components/button")} />
          </Navigation.Dropdown>
        </Navigation.Row>
      </Navigation>
      <Container id="page-content" className="page-content text-body">
        <h1>{title}</h1>
        {children}
      </Container>
      <Footer
        id="page-footer"
        className="page-footer"
        title="Helsinki Design System"
        footerAriaLabel="HDS Footer"
      >
        <Footer.Utilities backToTopLabel="Back to top">
          <Footer.Item label="Contact us" />
          <Footer.Item label="Give feedback" />
        </Footer.Utilities>
        <Footer.Base
          copyrightHolder="Copyright"
          copyrightText="All rights reserved"
        ></Footer.Base>
      </Footer>
    </main>
  );
};
export default Layout;
